<template>
  <a-button type="dashed" id="add_item" @click="addItem">增加一组</a-button>
  <a-collapse v-model:activeKey="activeKey">
    <a-collapse-panel v-for="(item, index) in items" :key="index" :header="item.title">
      <a-form
        :model="item.commandForm"
        name="basic"
        :label-col="{ span: 4 }"
        :wrapper-col="{ span: 16 }"
        autocomplete="off"
        @finish="onFinish"
        @finishFailed="onFinishFailed"
      >
        <a-form-item
          label="客户端ip"
          name="ip"
          :rules="[{ required: true, message: '请输入客户端ip' }]"
        >
          <a-input v-model:value="item.commandForm.ip" />
        </a-form-item>

        <a-form-item
          label="端口"
          name="port"
          :rules="[{ required: false, message: '' }]"
        >
          <a-input v-model:value="item.commandForm.port" />
        </a-form-item>

        <a-form-item
          label="包名"
          name="packageName"
          :rules="[{ required: false, message: '' }]"
        >
          <a-input v-model:value="item.commandForm.packageName" />
        </a-form-item>

        <a-form-item
          label="代理地址"
          name="proxyAddr"
          :rules="[{ required: false, message: '' }]"
        >
          <a-input v-model:value="item.commandForm.proxyAddr" />
        </a-form-item>

        <a-form-item
          label="自由命令"
          name="cmd"
          :rules="[{ required: false, message: '' }]"
        >
          <a-input v-model:value="item.commandForm.cmd" />
        </a-form-item>

        <a-form-item :wrapper-col="{ offset: 4, span: 16 }">
          <a-space wrap>
            <a-button type="primary" html-type="submit" @click="sendHttpRequest(item.commandForm, 'setProxy')">设置代理</a-button>
            <a-button type="primary" html-type="submit" @click="sendHttpRequest(item.commandForm, 'delProxy')">清除代理</a-button>
            <a-button type="primary" danger html-type="submit" @click="sendHttpRequest(item.commandForm, 'stop')">终止进程</a-button>
            <a-button type="primary" danger html-type="submit" @click="sendHttpRequest(item.commandForm, 'clear')">清理缓存</a-button>
            <a-button type="primary" html-type="submit" @click="sendHttpRequest(item.commandForm, 'free')">执行输入命令</a-button>
          </a-space>
        </a-form-item>
      </a-form>
    </a-collapse-panel>
  </a-collapse>
</template>

<script>
import { message } from 'ant-design-vue';
import { h, ref } from 'vue';
import { POST } from '@/utils/http'
const defaultCommandForm = {
      ip: "",
      port: "5555",
    }
export default {
  data() {
    return {
      activeKey: ref(['0']),
      items: [
        {
          title: 'adb控制',
          inputValue: '',
          commandForm: Object.assign({}, defaultCommandForm)
        }
      ],
    };
  },
  methods: {
    
    sendHttpRequest(commandForm, op) {
      commandForm.op = op
      POST('/adb', commandForm)
        .then(res => {
          message.info({
            content: () => {
              return h("pre", {class: "shell-output"}, res.data.data.split("\n").map(line => {
                return h("p", {}, line)
              }))
            }
          })
        }).catch(err => {
          console.log(err)
        })
    },
    addItem() {
      if (this.items.length >= 5) {
        message.error("已达到最大数量！")
        return
      }
      const newTitle = prompt('输入标题');
      if (newTitle) {
        this.items.push({
          title: newTitle,
          inputValue: '',
          commandForm: Object.assign({}, defaultCommandForm)
        });
      }
    }
  }
};
</script>

<style scoped>
#add_item {
  margin-bottom: 20px;
  width: 40%;
}
.shell-output {
  white-space: pre-wrap;
  text-align: left;
}
</style>